.top-banner {
    padding: 60px 0 80px 0;

    .content {
        padding-right: 130px;
        position: relative;
        .tag{
            background: #0063F7;
            border-radius: 50px;
            color: #fff;
            line-height: 24px;
            padding: 0 12px;
            display: inline-block;
            margin-bottom: 8px;
        }
        .title {
            font-size: 40px;
            font-weight: bold;
            margin-bottom: 20px;
            line-height: 1.3;
        }

        p {
            font-size: 16px;
            color: #555770
        }
        &__v2{
            padding-right: 15px
        }
    }

    .img {
        position: relative;

        &::before {
            content: "";
            background: url(images/graphics/bg-layer.png);
            width: 163px;
            height: 294px;
            position: absolute;
            left: -18px;
            top: -32px;
            z-index: -1;
        }
        &__v2{
            &::before{
                display: none;
            }
        }
    }
    .check{
        margin-bottom: 40px;
        li{
            position: relative;
            margin-bottom: 20px;
            color: #555770;
            font-size: 13px;
            padding-left: 24px;
            &::before{
                content: "";
                background: url(images/icons/checked.svg);
                width: 16px;
                height: 16px;
                position: absolute;
                left: 0;
                top: 1px;
            }
        }
    }
    .flex-btn{
        a{
            margin-right: 10px;
            &:last-child{
                margin-right: 0;
            }
        }
    }
    .doi-tac {
        position: absolute;
        bottom: 0;
        .grid__4{
            column-gap: 16px;
            row-gap: 16px;
        }
        p {
            font-size: 12px;
            margin-bottom: 10px;
        }
        &__v2{
            position: static;
            margin-top: 80px;
        }
    }
    @media (max-width: 1366px) {
        .content{
            .title{
                font-size: 32px;
            }
        }
    }
    @media (max-width: 991px) {
        padding: 30px 0 50px 0;
        .row{
            align-items: center;
        }
        .content{
            padding-right: 40px;
            .title{
                font-size: 24px;
            }
            &__v2{
                padding-right: 15px
            }
        }
        .doi-tac{
            position: static;
            margin-top: 24px;
        }
    }
    @media (max-width: 767px) {
        .content{
            padding-right: 15px;
            margin-bottom: 30px;
        }
        .doi-tac{
            position: static;
            margin-top: 24px;
        }
    }
    @media (max-width: 576px) {
        .grid__4{
            column-gap: 8px;
            row-gap: 8px;
        }
    }
}

.box-register-service {
    background: #F2F2F5 url(images/graphics/bg-dang-ky.png) no-repeat right top;
    border-radius: 6px;
    padding: 32px;
    display: flex;
    flex-wrap: wrap;

    .title {
        font-size: 20px;
        width: 250px;
        font-weight: bold
    }

    .box-search {
        width: 800px;
        width: calc(100% - 250px);
        padding-left: 24px;
    }

    .tag {
        display: flex;
        flex-wrap: wrap;
        margin-top: 12px;

        a {
            padding: 0 16px;
            background: #FAFAFC;
            border-radius: 73px;
            margin-right: 8px;
            margin-bottom: 8px;
            line-height: 32px;
            font-size: 13px
        }
    }
    @media (max-width: 767px) {
        padding: 20px;
        .title{
            width: 100%;
            margin-bottom: 20px;
        }
        .box-search{
            width: 100%;
            padding-left: 0;
        }
    }
}

.list-service {
    column-gap: 24px;
    row-gap: 24px;
    .swiper-container{
        padding: 40px 15px 30px 15px;
        margin: -10px -15px -30px -15px;
        .swiper-button-prev, .swiper-button-next{
            background: rgba(79,6,75,0.8);
            width: 32px;
            height: 32px;
            border-radius: 6px;
            border: 0;
            line-height: 32px;
            color: #fff;
            font-size: 20px;
            display: none;
        }
    }
    .itemt {
        text-align: center;
        background: #FFFFFF;
        border: 1px solid #E4E4EB;
        border-radius: 12px;
        padding: 24px;
        position: relative;
        .title {
            font-size: 14px;
            font-weight: bold;
            color: #4F064B;
            margin-bottom: 10px;
            height: 16px;
            overflow: hidden;
        }

        .des {
            height: 42px;
            overflow: hidden;
        }

        p {
            font-size: 14px;
            color: #555770
        }

        .price {
            font-size: 14px;
            color: #8F90A6;
            margin-bottom: 1rem;
            line-height: 1.3;

            .uppercase {
                font-size: 12px;
                text-transform: uppercase;
            }

            .all {
                font-size: 28px;
                font-weight: bold;
                color: #1C1C28;

                span {
                    font-weight: 400;
                    border-bottom: 1px solid;
                    line-height: 1;
                    display: inline-block;
                }
            }
        }

        .service{
            border-top: 1px solid #e5e5e5;
            padding-top: 24px;
            .title{
                margin-bottom: 20px;
            }
            .check{
                text-align: left;
                margin-bottom: 24px;
                li{
                    position: relative;
                    margin-bottom: 10px;
                    color: #555770;
                    font-size: 13px;
                    padding-left: 24px;
                    &::before{
                        content: "";
                        background: url(images/icons/checked.svg);
                        width: 16px;
                        height: 16px;
                        position: absolute;
                        left: 0;
                        top: 1px;
                    }
                }
            }
            .grid{
                column-gap: 16px;
                row-gap: 16px;
            }
        }
        &.focus,&:hover{
            border: 1px solid #fff;
            box-shadow: 0px 15px 20px rgba(0, 0, 0, 0.1);
            .tag{
                background: linear-gradient(131.88deg, #FF8800 -1.76%, #E63535 134.61%);
                border-radius: 6px 6px 0px 0px;
                color: #fff;
                line-height: 28px;
                position: absolute;
                top: -28px;
                left: 0;
                padding: 0 16px;
            }
            .price {
                .all{
                    color: #E63535;
                }
            }
        }
    }
    &__v2{
        column-gap: 1px;
        row-gap: 16px;
        .itemt{
            padding: 16px;
            border: 0;
            .des{
                height: 28px;
                font-size: 10px;
                text-transform: uppercase;
                margin-bottom: 10px;
            }
            .price{
                .price-down{
                    color: #E53535;
                }
            }
            .goi{
                display: flex;
                justify-content: center;
                font-size: 12px;
                color: #8F90A6;
                margin-bottom: 24px;
                span{
                    margin: 0 5px;
                }
            }
            .service{
                .title{
                    margin-bottom: 10px;
                    text-align: left;
                }
            }
            &.focus,&:hover{
                margin: 0 -1px;
                // box-shadow: 0px 15px 20px rgba(27, 28, 41, 0.2);
                position: relative;
                z-index: 2;
            }
        }

    }
    @media (max-width: 1199px) {
        .itemt{
            padding: 16px 10px;
        }
        &__v2{
            .swiper-container{
                .swiper-button-prev, .swiper-button-next{
                    display: block;
                }
            }
            .itemt {
                .price .all{
                    font-size: 24px;
                }
            }
        }
    }
    @media (max-width: 991px) {
        .swiper-container{
            .swiper-button-prev, .swiper-button-next{
                display: block;
            }
        }
    }

}

