        .article-content{
            width: calc(100% - 410px);
            padding-left: 100px;
            color: #fff;
            font-size: 24px;
            padding-top: 37px;
            padding-bottom: 37px;
            position: relative;
            &::before{
                content: "";
                background: rgba(255, 255, 255, .2);
                height: 1px;
                left: 100px;
                right: 0;
                position: absolute;
                top: 20px;
            }
            &::after{
                content: "";
