    .swiper-button-prev, .swiper-button-next{
        background: rgba(79, 6, 75, .8);
        width: 32px;
        height: 32px;
        border-radius: 6px;
        border: 0;
        line-height: 32px;
        color: #fff;
        font-size: 20px;
        top: 37px;
        margin-top: 0;
    }
    .swiper-button-prev{
        left: inherit;
        right: 40px;
    }
    .swiper-pagination{
        width: auto;
        left: 505px;
        bottom: -5px;
    }
    @media (max-width: 991px) {
        .article-itemt {
            .article-thumb{
                width: 45%;
            }
            .article-content{
                padding-left: 30px;width: 55%;
