                    left: 30px;
                }
                .logo{
                    margin-bottom: 20px;
                }
            }
        }
        .swiper-pagination{
            left: 0;
            width: 100%;
        }
    }
    @media (max-width: 576px) {
        .article-itemt {
            .article-thumb{
                width: 100%;
            }
            .article-content{
