// Range slide
.range-holder{
    margin: 2em auto;
    padding: 20px 0 20px 0;
    width: 960px;
    max-width: 100%;
    display: flex;
    position: relative;
    z-index: 4;
    .range-input{
        width: 170px;
        display: flex;
        margin-top: -26px;
        padding-right: 16px;
        span{
            font-size: 13px;
            text-align: right;
            padding-right: 10px;
            width: 100px;
        }
        input{
            color: #750E6C;
            font-weight: bold;
            width: 50px;
            width: calc(100% - 100px);
            height: 36px;
            padding: 0;
            text-align: center;
            border-radius: 6px;
            border: 1px solid #E4E4EB;
        }
    }
    .dragdealer{
        width: calc(100% - 170px);
        position: relative;
        height: 6px;
        background: #E4E4EB;
        border-radius: 8px;
    }
    .dragdealer .handle {
        position: absolute;
        top: 0;
        left: 0;
      }
      
      .stripe{
          background: #750E6C;
          position: absolute;
          top: 0;
          left: 0;
          height: 6px;
          border-radius: 8px;
          max-width: 100%;
      }
      
          #green-highlight{position: absolute; top:0; left: 67px; height: 6px; opacity: .5; background: #8dc63f; display: none;}
          #orange-highlight{position: absolute; top:0; left: 381px; height: 6px; opacity: .5; background: #e07c00; display: none;}
          #blue-highlight{position: absolute; top:0; left: 814px; height: 6px; opacity: .5; background: #008da8; display: none;}
      
      .square{
          position: absolute;
          top: 0;
          left: 0;
          margin-top: -13px;
          margin-left: 0;
          cursor: pointer;
          width: 30px;
          height: 30px;
          background: #fff;
          border: 4px solid #750E6C;
          border-radius: 50%;
      }
      
      .menu-line{width: 50%; margin: 2px auto; height: 5px; background: #FFF; display: block;}

      .infobox{
        width: 170px;
        height: 65px;
        background: #FFF;
        padding: 0px;
        border-radius: 8px;
        position: absolute;
        bottom: -100px;
        left: -72px;
    }
    
    .infobox:after {
        content: '';
        position: absolute;
        border-style: solid;
        border-width: 0px 10px 10px 10px;
        border-color: #FFFFFF transparent;
        display: block;
        width: 0;
        z-index: 1;
        top: -10px;
        left: 50%;
        margin-left: -10px;
    }
    
    .titlebar{
        padding: .5em;
        line-height: 1.5em;
        font-size: 14px;
    }
    #plan-holder{font-weight: bold;}
    
    .innerbox{
        padding:0 .5em;
    }
    
    .info-price{color: #E63535; font-size: 14px; line-height: 1.8em;}
    
    
    .annual-label{
        color: #505054; font-size: 14px; font-weight: bold; width: 110px; line-height: 1.8em;}
    .annual-price{
        color: #28293D;
        font-size: 16px;
        font-weight: bold;
        margin: -40px 0 0 -12px;
        width: 50px;
        text-align: center;
    } 

}
