.table-tinhnang{
    table{
        margin-bottom: 0;
        thead{
            th{
                border-bottom: 1px solid #dee2e6;
                &:first-child{
                    color: #212529;
                }
                .btn-default{
                    margin-bottom: 0;
                    margin-top: 10px;
                }
            }
        }
        th,td{
            color: #4F064B;
            text-align: center;
            border-top:0;
            &:first-child{
                text-align: left;
            }
        }
        tr{
            &:nth-child(2n){
                td{
                    background: #F5F7FA; 
                }
            }
            td{
                &:first-child{
                    width: 400px;
                }
            }
            &:last-child{
                td{
                    background: #fff;
                    border-top: 1px solid #dee2e6;
                }
            }
        }
    }
    @media (max-width: 991px) {
        table{
            th,td{
                white-space: nowrap;
                &:first-child{
                    white-space: pre-wrap;
                }
            }
        }
    }   
    @media (max-width: 767px) {
        .btn-default{
            font-size: 12px;
            padding: 0 15px;
            line-height: 32px;
        }
        table{
            th{
                div{
                    font-size: 11px;
                }
            }
            th,td{
                font-size: 13px;
                padding: .75rem 5px;
            }
        }
    }   
}

